.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: #c2c2c2;
  color: #1d1d1d;
  font-weight: 600;
  text-transform: uppercase;
}

.menu__top {
  margin-bottom: 24px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__phone-number {
  display: block;
  margin-bottom: 16px;
  line-height: 27px;
  color: inherit;
  text-decoration: none;
}

.menu__book-a-test-ride {
  display: block;
  width: fit-content;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 2px;
  color: inherit;
  text-decoration: none;
  padding-bottom: 7px;
  border-bottom: 1px solid #1d1d1d;
}

@media (min-width: 744px) {
  .menu__content {
    padding-inline: 72px;
  }
}

@media (min-width: 1260px) {
  .menu {
    font-weight: 700;
  }

  .menu__content {
    padding-inline: 120px;
  }
}
